.nft-gallery {
  margin: 2rem 0;
}

.nft-gallery h2 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: var(--dark-bg);
  border-bottom: 2px solid var(--primary-color);
  padding-bottom: 0.5rem;
  display: inline-block;
}

.nft-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

.loading {
  text-align: center;
  padding: 2rem;
  font-size: 1.2rem;
  color: var(--primary-color);
}

.error {
  background-color: #ff7675;
  color: white;
  padding: 2rem;
  border-radius: var(--border-radius);
  margin: 1rem 0;
  text-align: center;
  box-shadow: var(--box-shadow);
}

.error p {
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
}

.retry-button {
  background-color: white;
  color: #ff7675;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: var(--border-radius);
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
}

.retry-button:hover {
  background-color: #f8f9fa;
  transform: translateY(-2px);
}

.no-nfts {
  text-align: center;
  padding: 3rem;
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.no-nfts h2 {
  color: var(--dark-bg);
  margin-bottom: 1rem;
}

.no-nfts p {
  color: #636e72;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
}

.help-text {
  font-style: italic;
  color: #95a5a6;
  margin-top: 1rem;
  font-size: 0.95rem !important;
}

.mock-data-notice {
  background-color: #ffeaa7;
  color: #d35400;
  padding: 0.75rem;
  margin-bottom: 1.5rem;
  border-radius: var(--border-radius);
  text-align: center;
  border-left: 4px solid #fdcb6e;
}

.mock-data-notice p {
  margin: 0;
  font-weight: 600;
  font-size: 0.95rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .nft-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1.5rem;
  }
}
