.nft-card {
  background-color: white;
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--box-shadow);
  transition: var(--transition);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.nft-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

.nft-image-container {
  width: 100%;
  padding-top: 100%; /* 1:1 Aspect Ratio */
  position: relative;
  background-color: #f0f0f0;
  overflow: hidden;
}

.nft-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.nft-card:hover .nft-image {
  transform: scale(1.05);
}

.nft-details {
  padding: 1.5rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.nft-title {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--dark-bg);
}

.nft-description {
  color: #636e72;
  font-size: 0.9rem;
  margin-bottom: 1rem;
  flex-grow: 1;
}

.nft-id {
  font-size: 0.8rem;
  color: #b2bec3;
  margin-bottom: 1rem;
  font-family: monospace;
}

.nft-attributes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
  margin-top: auto;
}

.nft-attribute {
  background-color: #f5f6fa;
  padding: 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  display: flex;
  flex-direction: column;
}

.attribute-type {
  color: #636e72;
  margin-bottom: 0.2rem;
}

.attribute-value {
  font-weight: 600;
  color: var(--primary-color);
}
